* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding-left: -10px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  list-style: none;
}

:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

p {
  margin: 0;
  padding: 0;
}

@media screen and (min-width: 300px) {
  .searching-box {
    background-color: #3953c8;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .search-form {
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
  }

  input {
    width: 220px;
    height: 32px;
    border: none;
    border-radius: 5px 0 0 5px;
    outline: none;
    padding: 8px;
  }

  .search-btn {
    cursor: pointer;
    border: none;
    border-radius: 0 5px 5px 0;
    margin: 0;
    padding: 0 10px;
  }

  .gallery {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr;
    justify-content: center;
    gap: 6px;
    margin: 10px 10px 10px -20px;
    display: grid;
  }

  .photo-card {
    width: calc(90vw - 10px);
    height: calc(85vw - 5px);
    border-radius: 5px;
    margin: 5px;
    transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
    box-shadow: 3px 3px 3px rgba(184, 178, 178, .7);
  }

  .gallery__image {
    width: 100%;
    height: 70%;
  }

  a {
    color: #212121;
    text-align: center;
    text-decoration: none;
  }

  .info {
    justify-content: space-around;
    font-size: 12px;
    display: flex;
  }

  .info-item {
    margin-top: 0;
  }
}

@media screen and (min-width: 400px) {
  .info {
    font-size: 15px;
  }

  input {
    width: 240px;
  }
}

@media screen and (min-width: 520px) {
  input {
    width: 260px;
  }

  .gallery {
    grid-template-columns: 1fr 1fr;
  }

  .photo-card {
    width: calc(45vw - 5px);
    height: calc(39vw - 5px);
  }

  .info {
    font-size: 10px;
  }
}

@media screen and (min-width: 700px) {
  .info {
    font-size: 15px;
  }

  input {
    width: 280px;
  }
}

@media screen and (min-width: 900px) {
  input {
    width: 300px;
  }

  .gallery {
    grid-template-columns: 1fr 1fr 1fr;
  }

  .photo-card {
    width: calc(31vw - 5px);
    height: calc(26vw - 5px);
  }

  .info {
    font-size: 12px;
  }
}

@media screen and (min-width: 1200px) {
  input {
    width: 320px;
  }

  .gallery {
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  .photo-card {
    width: calc(24vw - 5px);
    height: calc(20vw - 5px);
  }
}

.btn-container {
  justify-content: center;
  display: flex;
}

.load-more {
  width: 202px;
  min-height: 50px;
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  text-align: center;
  background-color: #191970;
  border: none;
  border-radius: 6px;
  margin-bottom: 10px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.875;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.up-btn {
  width: 40px;
  height: 40px;
  cursor: pointer;
  text-align: center;
  background-color: #191970;
  border: none;
  border-radius: 50px;
  margin-bottom: 10px;
  position: fixed;
  bottom: 0;
  right: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

